import { Avatar, Card } from "antd";
const { Meta } = Card;

const ListCard = () => {
    return (
        <div style = {{ marginBottom: "1rem" }}>
            <Card hoverable>
                <Meta
                    avatar = { 
                        <Avatar 
                            shape = "square" 
                            size = {{ md:140, lg:140, xl:140, xxl:140}}
                            src = "https://upload.wikimedia.org/wikipedia/commons/3/3b/2005_Skoda_Octavia_Ambiente_TDi_Estate_2.0_Rear.jpg"
                        />
                    }
                    title = "name" 
                    description = "Blablala"
                />
            </Card>
        </div>
    );
};

export default ListCard;